<template lang="html">
  <div id="QA">
      <div class="BG"></div>
      <h1>Q &amp; A</h1>

      <article>
          <h2><strong></strong> 非來不可的理由？</h2>
          <section>
              <p>
                  與企業交流，直接認識業界的想法和需求，並以所學解決實際的問題，創造價值。 認識與你理念相近的伙伴，歷經兩天一夜的密集合作，凝聚出深厚的革命情感。 體驗微型創業，培養團隊合作的能力，就算未來沒有真的選擇創業，也會是職場上炙手可熱的人才！
              </p>
              <p>
                  舒適的會場、好吃的點心任你享用，這是最棒的工作環境！
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 活動當天在哪住宿？</h2>
          <section>
              <p>
                  Hackathon 理論上是不會停下來睡覺的喔！不過，如果覺得太累想要休息， 也可以移動至休息區，趴在桌上小睡，或者自己帶睡袋來打地舖。
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 活動進行中可以暫時離開會場嗎？</h2>
          <section>
              <p>
                  可以。這不是「入闈」，除了開場和閉幕，如果有需要，可以自行暫時離開，但要記得回來！
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 活動具體內容是什麼？</h2>
          <section>
              <p>
                  這是秘密喔！快組隊報名來體驗一下就知道了。可以確定的是：來參加之後，一定會更認識自己、 技術更精進、認識不少新朋友，不虛此行！
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 我不是交大的學生，請問我要怎麼到達會場？</h2>
          <section>
              <p>請先至交通大學，地址：新竹市大學路1001號</p>
              <p>
                  地圖：<a href="http://www.nctu.edu.tw/maps" target="_blank">交大校內地圖</a>，<a href="https://www.google.com.tw/maps?q=%E5%9C%8B%E7%AB%8B%E4%BA%A4%E9%80%9A%E5%A4%A7%E5%AD%B8&bav=on.2,or.r_cp.&bvm=bv.58187178,d.dGI,pv.xjs.s.en_US.EeLgqkzqnSg.O&biw=758&bih=453&dpr=1&um=1&ie=UTF-8&hl=zh-TW&sa=N&tab=wl" target="_blank">Google 地圖</a>
              </p>
              <p>
                  搭乘國道客運的話，建議在「科學園區站」下車，然後從大學路沿途步行到底即交通大學正門； 或者可以在「清華大學」站下車，然後穿越清大，走過清交小徑抵達交大。
              </p>
              <p>
                  坐火車來的話，在新竹火車站下車，然後轉搭市區公車2路，終點站即交通大學；2路班次較少， 也可以搭1路，至「過溝」站下車，然後沿大學路步行至底。車資單趟一律15元， 可以刷各縣市的智慧儲值卡。
                  新竹市區公車時刻表及路線查詢
              </p>
              <p>
                  若搭乘高鐵，請至新竹站下車，然後轉搭高鐵接駁公車，往東門市場方向，於「交通大學」站下車。或者搭乘交大校內接駁公車，可直達交大。
                  詳見：高鐵新竹站快捷公車
              </p>
              <p>抵達交通大學後，請參照校內地圖前往「工程四館」。</p>
              <p>如果對交通方式還有疑問，歡迎來信詢問工作人員。</p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 我需要買回家的車票，請問活動會在幾點左右結束呢？</h2>
          <section>
              <p>活動會在 2018/10/28 週日 晚上18:00左右結束！</p>
          </section>
      </article>


      <article>
          <h2><strong></strong> 參加這個活動，需要什麼樣的能力？</h2>
          <section>
              <p>
                  自行組成的參賽團隊中，至少需有一人具備所選企業的技術條件能力。在填寫團隊報名表單時，我們會讓大家選擇企業的志願序，並依此為您分配適合的引導企業。
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 我要怎麼知道我報名成功了呢？</h2>
          <section>
              <p>
                報名流程分成「網路報名」和「繳費」兩個階段。 大會收到您在網路上填寫的報名資料之後， 會寄信通知您已完成「網路報名」。
              </p>
              <p>
                網路報名完成後，第一階段報名成功的團隊，請記得於 10/5 前完成繳費。第二階段報名成功的團隊，請記得於 10/12 前完成繳費，這樣報名流程才算完成喔！
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 如果我成功錄取了，才發現當天有事無法到場參與怎麼辦？</h2>
          <section>
              <p>若您在匯完報名費後發現不克參與，請在兩個星期前寄信聯絡工作人員！</p>
              <p>我們每人將會退200的報名費給您。(另外需扣除15的轉帳手續)</p>
              <p>工作人員信箱：<a href="mailto:hacknctu@googlegroups.com">hacknctu@googlegroups.com</a></p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 如果我沒被錄取怎麼辦？ QAQ</h2>
          <section>
              <p>
                  我們希望有心參與活動的人都能夠如願，但因適合的題目有限，所以可能會出現人數過多的狀況。 如果有釋出候補名額，我們會稍後公佈，請密切關注網站上的消息和大會發出的通知！
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 如果我對企業所提供的資源遇到瓶頸怎麼辦?</h2>
          <section>
              <p>不用擔心，各家企業都會安排技術人員到場支援，可以直接當面請教他們，也可藉機好好交流。</p>
          </section>
      </article>


      <article>
          <h2><strong></strong> 如果我覺得場地太吵我無法認真 CODING 怎麼辦?</h2>
          <section>
              <p>
                  別擔心，大會這邊有提供團隊另外的小型會議室使用(一隊可自己一間)，需要至服務台申請，一次借用為兩小時，先搶先贏且採排隊制，人數過多須輪流使用。
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 如果團隊中有人無法準時到場怎麼辦呢？</h2>
          <section>
              <p>
                  若無法準時到，有「事前」跟主辦單位告知的話是可以讓他補簽到的，但到場之後必須待到閉幕才可以領回保證金，若沒有告知則無法補簽到喔！
              </p>
          </section>
      </article>

      <article>
          <h2><strong></strong> 請問有提供衛浴嗎？</h2>
          <section>
              <p>顧及個人衛生與安全，本活動恰不提供任何衛浴設施供參賽者使用。</p>
          </section>
      </article>

      <footer>
          &copy; 2018 MeiChu Hackathon
      </footer>
  </div>
</template>

<script>
export default {
    name: 'QA',
    mounted() {
        var elems = document.querySelectorAll('article > h2 > strong');
        var i = 1;
        for(var elem of elems) {
            elem.innerHTML = "Q" + i;
            i++
        }
    }
}
</script>

<style lang="scss">
$color_w: #f7f7f7;
$color_gd: #B2E4DB;
$color_gl: #A9FBD7;
$color_b: #88C1FF;
$color_r: #FF6666;
$light-green: #1DCB8B;

#QA {
    color: white;
    padding-top: 2.6em;
    font-family: 'Quicksand', 'Microsoft JhengHei';
    .BG {
        background-image: url("https://i.imgur.com/xRLeODk.png");
        width: 100%; height: 100%;
        position: fixed;
        top: 0;
        background-size: cover;
    }
    a {
        color: $color_r;
    }

    h1 {
        text-align: center;
        margin-bottom: 1em;
        font-size: 2em;
        font-weight: 500;
    }

    h2 {
        padding-bottom: 0;
        margin-bottom: 0;
        font-size: 1.6em;
        strong {
            font-size: 1.2em;
            vertical-align: text-bottom;
            color: $color_r;
        }
    }

    article {
        max-width: 700px;
        margin: 0 auto 5em auto;
        font-family: 微軟正黑體;
        section {
            padding-left: 1em;
            position: relative;

            &::after {
                content: ' ';
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 10px;
                height: 100%;
                background-color: $light-green;
            }
        }
    }

    p {
        margin: 1em 0 0 0;
        line-height: 1.6em;
        text-align: justify;
    }

    footer {
        font-size: 1em;
        display: block;
        text-align: center;
        padding-bottom: 4em;
    }

}

</style>



