<template lang="html">
    <div class="container" id="join_info">
        <div class="content_2 justify-content-md-center row">
            <div class="partical col-md-7">
                <h1>報名流程</h1>
                <div class="partical_block">
                    <p>每人報名費用 : 400 + 200 保證金(全程參與活動會全額退還保證金)<br/>
                        報名日期 : 09/19 - 09/30
                    </p>
                    <p>報名資格 : 不限年齡、不限領域，歡迎各類專才與技術人員踴躍報名。</p>
                    <br/>
                    <p>10/1 公告錄取參賽隊伍以及所屬企業<br/>
                      10/5 第一階段報名成功隊伍繳費期限<br/>
                      10/6 公告後補名單<br/>
                      10/12 候補報名成功隊伍繳費期限
                    </p>
                </div>
                <div class="sign_in">
                    <a>立即報名</a>
                </div>
            </div>
            <div class="partical col-md-7">
                <h1>報名須知</h1>
                <ul class="partical_block">
                    <li>
                        報名表單送出後請密切注意您所填寫之電子信箱，大會將會以email與參賽者確認報名/參賽事宜。(若未收到信請先至垃圾信件匣查找，若還是沒收到可與<a href="https://www.facebook.com/HackMeiChu/">FB粉專</a>聯絡)
                    </li>
                    <li>
                        若報名後因個人因素導致活動當日無法前來者，請在活動日期兩周前(10/14以前)寄email/私訊粉專聯絡工作人員，我們將會派專人與您聯繫退費事宜。(可能產生轉帳手續費)
                    </li>
                    <li>企業分配將以各團隊所填寫之企業志願序為準。</li>
                    <li>參賽隊伍可自由使用各企業提供的資源做整合，但是一定要使用到自己所屬引導企業的資源，企業資訊請詳見「合作引導企業」</li>
                    <li>團隊報名者：每一隊3~5人，團隊中至少要有一人具備使用企業資源的能力。並且儘量在專長中說明團隊或個人所具備的技術或其他能力。</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'join_info'
}
</script>



